<template>
  <div class="axn">
    <van-row>
      <van-col
        span="12"
        v-for="(item, index) in cateLists"
        :key="index"
        @click="Axn(item)"
      >
        <img :src="item.icon" alt="" />
        <h3>#{{ item.catename }}#</h3>
      </van-col>
    </van-row>
  </div>
</template>

<script>
// import Item from "@/components/Item.vue";
export default {
  // components: {
  //   Item
  // },
  data() {
    return {
      cateLists: [],
      daysitemslist: null,
      clickitem: null,
      showitem: false,
      startPagx: null
    };
  },
  methods: {
    // touchredmore(event) {
    //   this.startPagx = event.touches[0].pageX;
    //   // window.console.log(this.startPagx);
    // },
    // touchendsredmore(event) {
    //   let x = event.changedTouches[0].pageX;
    //   if (x - this.startPagx >= 100) {
    //     this.$router.go(-1);
    //     // this.isactive = false;
    //   }
    // },
    Axn(i) {
      this.showitem = true;
      this.clickitem = i;
      this.$store.commit("submiTclickitem", i);
      this.$store.commit("submiShowitem", true);
      if (i.tab) {
        window.console.log(i.tab);
        this.getTab(i.tab);
        this.$router.push({ path: "/itemplaye", query: { id: i.tab } });
      } else {
        window.console.log(i.cateid);
        this.getCateId(i.cateid);
        this.$router.push({ path: "/itemplaye", query: { id: i.cateid } });
      }

      // this.$router.push("/itemplaye");
    },
    getTab(i) {
      this.axios
        .get(
          "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/post/getPostByTab&p=1&size=10&tab=" +
            i
        )
        .then(response => {
          window.console.log(response.data.data);
          this.daysitemslist = response.data.data;
          this.$store.commit("submiTdaysitemslist", response.data.data);
        });
    },
    getCateId(i) {
      this.axios
        .get(
          "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/post/getPostInCate?p=1&size=10&cateid=" +
            i
        )
        .then(response => {
          window.console.log(response.data.data);
          this.daysitemslist = response.data.data;
          this.$store.commit("submiTdaysitemslist", response.data.data);
        });
    },
    getCateList() {
      this.axios
        .get(
          "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/cate/getList"
        )
        .then(response => {
          // window.console.log(response);
          this.cateLists = response.data.data;
        });
    }
  },
  created() {
    this.getCateList();
  }
};
</script>

<style lang="less">
.axn {
  height: 100vh;
  overflow: hidden;
  overflow-y: scroll;
  position: relative;
}

.van-col {
  position: relative;
  height: 50vw;
  img {
    vertical-align: middle;
    height: 100%;
  }
  h3 {
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -31px;
    margin-top: -14px;
    color: white;
    font-size: 1.2em;
  }
}
</style>
